import { modalElements } from "../constants/ui-elements.js";
import { showLoggerMessage, POSITION } from "../users-messages/notification-popups.js";
import { SUCCESS, ERRORS } from "../constants/messages.js";
import { modalElements } from "../constants/ui-elements.js";
import Cookies from 'js-cookie';
import axios from "axios";
import 'dotenv/config';

async function changeName() {
	const userName = modalElements.inputChangeName.value;
	const token = Cookies.get('userToken');

	try {
		const response = await axios.patch(process.env.USER_URL, {
			name: userName,
		}, {
			headers: {
				'Content-Type': 'application/json;charset=utf-8',
				'Authorization': `Bearer ${token}`
			},
		});

		if (response.status === 200) return response.data;
	} catch {
		showLoggerMessage(ERRORS.ICON, ERRORS.CHANGE_NAME, POSITION.CENTER);
		modalElements.formChangeName.reset();
	}

}

async function handleChangeNameSubmit(e) {
	e.preventDefault();
	const data = await changeName();

	if (data) {
		showLoggerMessage(SUCCESS.ICON, SUCCESS.CHANGE_NAME, POSITION.CENTER);
		localStorage.setItem('user-name', data.name);
	}
}

export { handleChangeNameSubmit };