import Swal from 'sweetalert2';

const POSITION = {
	TOP: 'top',
	CENTER: 'center'
}

function showLoggerMessage(iconResult, title, position) {
	Swal.fire({
		title: title,
		toast: true,
		position: position,
		icon: iconResult,
		showConfirmButton: false,
		timer: 2000,
		timerProgressBar: true,
	})
}

export { showLoggerMessage, POSITION };