import { openAuthorizationModal } from "../utilits.js";
import { deleteAllCookies } from "../../utils-functions.js";
import { chatElements } from "../../constants/ui-elements.js";
import { WEB_SOCKET } from "../../constants/messages.js";
import { socket } from "../../websocket.js";
import { heighOfTwentyMessages } from "../../users-messages/render-messages.js";

const { CODE_CLOSE_NORMAL, WORK_ENDED } = WEB_SOCKET;
const websocketOpen = socket && socket.readyState === WebSocket.OPEN;

function handleLogOutClick() {
	openAuthorizationModal();
	if (websocketOpen) socket.close(CODE_CLOSE_NORMAL, WORK_ENDED);

	deleteAllCookies();
	localStorage.clear();
	chatElements.areaMessanges.replaceChildren();
	heighOfTwentyMessages = null;
}

export { handleLogOutClick };