import { showLoggerMessage, POSITION } from "../users-messages/notification-popups.js";
import { ERRORS } from "../constants/messages.js";
import Cookies from 'js-cookie';
import axios from 'axios';
import 'dotenv/config';

async function getUsersMessages() {
	const token = Cookies.get('userToken');

	try {
		const response = await axios.get(process.env.MESSAGES, {
			headers: {
				'Content-Type': 'application/json;charset=utf-8',
				'Authorization': `Bearer ${token}`
			}
		})
		if (response.status === 200) return response.data;
	} catch (error) {
		showLoggerMessage(ERRORS.ICON, ERRORS.NO_MESSAGES, POSITION.TOP);
		console.error(err);
	}
}

async function handleGetUsersMessages() {
	const data = await getUsersMessages();

	if (data) {
		try {
			const dataString = JSON.stringify(data.messages);
			localStorage.setItem('message-history', dataString);
		} catch (err) {
			console.error(err);
		}
	}
}

export { handleGetUsersMessages };

