import { chatElements } from "../constants/ui-elements.js";
import { handleSendMessageSubmit } from "./send-message.js";

const { messageInput } = chatElements;

const recognition = new webkitSpeechRecognition() || new SpeechRecognition();
recognition.lang = "ru-RU";
recognition.maxAlternatives = 1;
recognition.interimResults = true;

recognition.onresult = (e) => {
	const textFromRecord = e.results[e.results.length - 1][0].transcript;
	messageInput.value = textFromRecord;
}

const startRecord = () => recognition.start();
const endRecord = () => recognition.stop();

const handleSendMessageByVoice = (e) => {
	endRecord();
	handleSendMessageSubmit(e);
}


export { startRecord, handleSendMessageByVoice };