import { scrollToEnd } from "../utils-functions.js";
import { chatElements } from "../constants/ui-elements.js";
import { validateSendingEmptyMessage } from "../utils-functions.js";
import { socket } from "../websocket.js";

function sendMessage() {
	const message = chatElements.messageInput.value;
	if (!message) return;

	try {
		socket.send(JSON.stringify({ text: message }));
	} catch (err) {
		console.error(err);
	}
}

function handleSendMessageSubmit(e) {
	e.preventDefault();
	sendMessage();
	chatElements.formForMessage.reset();
	validateSendingEmptyMessage();
	scrollToEnd()
}

export { handleSendMessageSubmit };


