import { renderSingleMessage } from "./users-messages/render-messages.js";
import { scrollToEnd } from "./utils-functions.js";
import Cookies from "js-cookie";
import 'dotenv/config';


let socket;

function connectToWebsocket() {
	const token = Cookies.get('userToken');
	const url = process.env.WEB_SOCKET + token;
	socket = new WebSocket(url);

	console.log('cоединение установлено');

	socket.onclose = () => {
		setTimeout(() => connectToWebsocket(), 1000);
		console.log('Соединение закрыто');
	}

	socket.onmessage = (e) => {
		const message = JSON.parse(e.data);
		renderSingleMessage(message);
		scrollToEnd();
	}

}

function handleWebsocketConection() {
	const websocketClose = !socket || socket.readyState !== WebSocket.OPEN;

	if (websocketClose) {
		connectToWebsocket();
	}
}



export { handleWebsocketConection, socket }