import { modalElements, authorization, chatElements } from './constants/ui-elements.js';
import { handleChangeNameSubmit } from './user/change-name.js';
import { handleSendTokenSubmit, handleLogInSubmit } from './authorization/log-in/authorization.js';
import { switchToAuthorizarion, switchToEnterCode } from './authorization/utilits.js';
import { openModal, closeModal } from './modal.js';
import { validateSendingEmptyMessage } from './utils-functions.js';
import { handleSendMessageSubmit } from './user/send-message.js';
import { lazyLoadingMessages } from './users-messages/render-messages.js';
import { handleLogOutClick } from './authorization/log-out/log-out.js';
import { saveUserAccount } from './main.js';
import { handleSendMessageByVoice, startRecord } from './user/record-voice.js';

const eventSaveUserAccount = window.addEventListener('load', saveUserAccount);

const eventOpenModal = modalElements.btnSettings.addEventListener("click", openModal);
const eventCloseModal = modalElements.modalOverlay.addEventListener("click", closeModal);

const eventValidateEmptyMessage = chatElements.messageInput.addEventListener("input", validateSendingEmptyMessage);

const eventHandleSendMessageSubmit = chatElements.formForMessage.addEventListener("submit", handleSendMessageSubmit);

const eventGoToEnterCode = authorization.btnEnterCode.addEventListener("click", switchToEnterCode);
const eventGoToEnterBack = authorization.btnСomeBack.addEventListener("click", switchToAuthorizarion);

const eventHandleLogOut = chatElements.btnExitChat.addEventListener("click", handleLogOutClick);

const eventSendCodeToUserEmail = authorization.formAuthorization.addEventListener("submit", handleSendTokenSubmit);

const eventLogInToChat = authorization.formLogIn.addEventListener("submit", handleLogInSubmit);
const eventChangeName = modalElements.formChangeName.addEventListener("submit", handleChangeNameSubmit);

const eventLazyLoad = chatElements.messageList.addEventListener('scroll', lazyLoadingMessages);

chatElements.btnMicrophone.addEventListener('mousedown', startRecord);
chatElements.btnMicrophone.addEventListener('mouseup', handleSendMessageByVoice);

chatElements.btnMicrophone.addEventListener('touchstart', startRecord);
chatElements.btnMicrophone.addEventListener('touchend', handleSendMessageByVoice);