import { getTemplateContent, scrollToEnd } from "../utils-functions.js";
import { chatElements } from "../constants/ui-elements.js";
import { format } from 'date-fns';
import Cookies from "js-cookie";


let heighOfTwentyMessages = null;

function lazyLoadingMessages() {

	if (heighOfTwentyMessages === null) {
		heighOfTwentyMessages = chatElements.areaMessanges.scrollHeight
	}

	if (chatElements.messageList.scrollTop === 0) {
		renderMessages();
		chatElements.areaMessanges.scrollTo(0, heighOfTwentyMessages);
	}

}

function renderMessages() {
	const countOfRenderMessages = 19;
	const messages = localStorage.getItem('message-history');
	if (!messages) return;

	try {
		const arrayMassages = JSON.parse(messages).reverse();

		const needToRenderMessages = arrayMassages.slice(arrayMassages.length - countOfRenderMessages);
		const newArr = arrayMassages.slice(0, arrayMassages.length - countOfRenderMessages);

		needToRenderMessages.reverse().forEach(message => {
			renderSingleMessage(message, 'prepend');
		});

		localStorage.setItem('message-history', JSON.stringify(newArr))
	} catch (error) {
		console.error('Error parsing messages from localStorage:', error);
	}
}

function renderSingleMessage(message, insertionMethod = 'append') {
	const templateContent = getTemplateContent();
	templateContent.message.textContent = message.text;
	templateContent.sendTime.textContent = format(new Date(message.createdAt), 'H:mm');

	if (message.user.email === Cookies.get('user-email')) {
		templateContent.messageSendFrom.classList.add('user-message');
	} else {
		templateContent.messageSendFrom.classList.add('other-message');
		templateContent.user.textContent = message.user.name;
	}

	if (insertionMethod === 'prepend') {
		chatElements.areaMessanges.prepend(templateContent.li);
	} else if (insertionMethod === 'append') {
		chatElements.areaMessanges.append(templateContent.li);
	}

}

export { renderMessages, renderSingleMessage, lazyLoadingMessages, heighOfTwentyMessages };