import { authorization } from "../../constants/ui-elements.js";
import { showLoggerMessage, POSITION } from "../../users-messages/notification-popups.js";
import { ERRORS, SUCCESS } from "../../constants/messages.js";
import { initializeChatApp } from "../../main.js";
import Cookies from 'js-cookie';
import axios from "axios";
import 'dotenv/config';


async function sendTokenToUserEmail() {
	const userEmail = authorization.inputEmail.value;

	try {
		const response = await axios.post(process.env.USER_URL, {
			email: userEmail,
		}, {
			headers: { 'Content-Type': 'application/json;charset=utf-8' },
		});
		if (response.status === 200) return response.data;
	} catch {
		showLoggerMessage(ERRORS.ICON, ERRORS.INVALID_EMAIL, POSITION.CENTER);
	} finally {
		authorization.formAuthorization.reset();
	}
}

async function handleSendTokenSubmit(e) {
	e.preventDefault();
	const data = await sendTokenToUserEmail();
	if (data) {
		showLoggerMessage(SUCCESS.ICON, SUCCESS.MESSAGE_SEND, POSITION.CENTER);
	}
}


async function logInToChat() {
	const token = authorization.inputLogIn.value;

	try {
		const response = await axios.get(process.env.USER_INFORM, {
			headers: {
				'Content-Type': 'application/json;charset=utf-8',
				'Authorization': `Bearer ${token}`
			}
		})

		if (response.status === 200) return response.data;
	} catch {
		showLoggerMessage(ERRORS.ICON, ERRORS.INVALID_EMAIL, POSITION.CENTER);
	} finally {
		authorization.formLogIn.reset();
	}
}

async function handleLogInSubmit(e) {
	e.preventDefault();

	try {
		const data = await logInToChat();

		if (data) {
			Cookies.set('userToken', data.token);
			Cookies.set('user-email', data.email);
			await initializeChatApp();
		}
	} catch (error) {
		console.error(error);
	}
}


export { handleSendTokenSubmit, handleLogInSubmit };

