import { authorization } from './constants/ui-elements.js';
import { SUCCESS } from './constants/messages.js';
import { showLoggerMessage, POSITION } from './users-messages/notification-popups.js';
import { handleGetUsersMessages } from './users-messages/data-messages.js';
import { renderMessages } from './users-messages/render-messages.js';
import { handleWebsocketConection } from './websocket.js';
import { openAuthorizationModal, closeAuthorizationModals } from './authorization/utilits.js';
import { getLastUserName, scrollToEnd } from './utils-functions.js';
import Cookies from 'js-cookie';

getLastUserName();

async function saveUserAccount() {
	if (Cookies.get('userToken')) {
		await initializeChatApp();
	} else {
		openAuthorizationModal();
	}
}

async function initializeChatApp() {
	closeAuthorizationModals();
	showLoggerMessage(SUCCESS.ICON, SUCCESS.AUTHORIZATION, POSITION.TOP);
	await handleGetUsersMessages();
	renderMessages();
	handleWebsocketConection();
	scrollToEnd();
}


export { initializeChatApp, saveUserAccount };